import React, { useState, useEffect } from "react";
import "./Movie.css";
import Movies from "../../comp/Movies";
import StarRatingComponent from "react-star-rating-component";
const fetch = require("node-fetch");

function Movie({ match }) {
	const APIKEY = "601afad9072abecf3270711c574913eb";

	useEffect(() => {
		fetchItem();
	}, []);

	const [item, setItem] = useState({});

	const fetchItem = async () => {
		const fetchItem = await fetch(
			`https://api.themoviedb.org/3/movie/${match.params.id}?api_key=${APIKEY}&language=en-US`,
		);
		const item = await fetchItem.json();
		setItem(item);
	};

	if (Object.entries(item).length !== 0) {
		document.getElementById("Movie").style.opacity = "1";
	}

	return (
		<div id="Movie" className="load">
			<header>
				<div id="header_container">
					<div id="poster">
						<img
							src={`https://image.tmdb.org/t/p/w185/${item.poster_path}`}
							alt={item.title}
						/>
					</div>
					<div id="header_content">
						<main id="header_content_wrapper" className="add_pad">
							<h1>{item.title}</h1>
							<StarRatingComponent
								name="star_rating"
								editing={false}
								renderStarIcon={() => <span>★</span>}
								starCount={5}
								value={item.vote_average / 2}
								emptyStarColor={"rgb(238 238 238 / 60%)"}
							/>
							<p>{item.overview}</p>
							<br />
							<small>Release date: {item.release_date}</small>
							<br />
						</main>
					</div>
				</div>
				<div
					id="header_background"
					style={{
						backgroundImage: `url(https://image.tmdb.org/t/p/w780/${item.backdrop_path})`,
					}}
				></div>
			</header>
			<main id="mobile_content" className="add_pad">
                <h1>{item.title}</h1>
                <StarRatingComponent
                    name="star_rating"
                    editing={false}
                    renderStarIcon={() => <span>★</span>}
                    starCount={5}
                    value={item.vote_average / 2}
                    emptyStarColor={"rgb(238 238 238 / 60%)"}
                />
                <p>{item.overview}</p>
                <br />
                <small>Release date: {item.release_date}</small>
                <br />
            </main>
            <div className="add_pad">
                <Movies
					title="Recommended movies"
					media_form="movie"
                    url={`https://api.themoviedb.org/3/movie/${match.params.id}/recommendations?api_key=${APIKEY}&language=en-US&page=1`}
                />
            </div>
		</div>
	);
}

export default Movie;
