import React from "react";
import "./Searchbar.css";

class Searchbar extends React.Component {
	handleKeyPress = (event) => {
		if(event.key === 'Enter'){
			const searchQuery = document.querySelector("#search_query");
			if (searchQuery.value.trim() !== "") {
				window.location.href = `../screentime/#/search/${searchQuery.value.trim()}`;
			}
			searchQuery.value = "";
		}
	}
	render() {
		const doSearch = () => {
			const searchQuery = document.querySelector("#search_query");
			if (searchQuery.value.trim() !== "") {
				window.location.href = `../screentime/#/search/${searchQuery.value.trim()}`;
			}
			searchQuery.value = "";
		};
		

		return (
			<div id="searchbar">
				<input type="search" placeholder="Movies & shows" id="search_query" onKeyUp={this.handleKeyPress} />
				<button onClick={doSearch}>Search</button>
			</div>
		);
	}
}

export default Searchbar;
