import React from "react";
import "./Header.css";
import { Link } from "react-router-dom";
const fetch = require("node-fetch");

class Header extends React.Component {
	constructor() {
		super();
		this.state = { movies: [] };
	}
	componentDidMount() {
		fetch(this.props.url)
			.then((response) => response.json())
			.then((json) => this.setState({ movies: json["results"] }));
	}

	render() {
		if (this.state.movies.length) {
			return (
				<header
					style={{
						backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgb(20, 20, 20) 100%), url(https://image.tmdb.org/t/p/original/${this.state.movies[0].backdrop_path})`,
					}}
				>
					<div className="header_content">
						<h2>{this.state.movies[0].title}</h2>
						<Link to={`./movie/${this.state.movies[0].id}`}>
							{" "}
							View details{" "}
						</Link>
					</div>
				</header>
			);
		}
		return <header></header>;
	}
}

export default Header;
