import React from 'react';
import Movies from '../../comp/Movies';
import './Home.css';
import Header from './Header';

class Home extends React.Component {
  render() {
    const APIKEY = '601afad9072abecf3270711c574913eb';
    return <div id="Home">
        <Header title="test"  url={`https://api.themoviedb.org/3/movie/now_playing?api_key=${APIKEY}&language=en-US&page=1`} />
        <main>
            <Movies title="Now playing movies" media_form="movie" url={`https://api.themoviedb.org/3/movie/now_playing?api_key=${APIKEY}&language=en-US&page=1`} />
            <Movies title="Popular Shows" media_form="tv" url={`https://api.themoviedb.org/3/tv/popular?api_key=${APIKEY}&language=en-US&page=1`} />
            <Movies title="Popular movies" media_form="movie" url={`https://api.themoviedb.org/3/movie/popular?api_key=${APIKEY}&language=en-US&page=1`} />
            <Movies title="Top rated movies" media_form="movie" url={`https://api.themoviedb.org/3/movie/top_rated?api_key=${APIKEY}&language=en-US&page=1`} />
            <Movies title="Upcoming movies" media_form="movie" url={`https://api.themoviedb.org/3/movie/upcoming?api_key=${APIKEY}&language=en-US&page=1`} />
        </main>
    </div>
  }
}

export default Home;
