import React from "react";
import "./Nav.css";
import Search from "./Searchbar";
import { Link } from "react-router-dom";

class Nav extends React.Component {
	render() {
		const show = () => {
			document.querySelector('#Navbar').classList.toggle("active_menu")
		}
		return (
			<nav id="Navbar">
				<div className="add_pad">
					<ul>
						<Link to="/">
							<li>
								<strong>Screen-Time</strong>
							</li>
						</Link>
						<Link to="/about">
							<li>About</li>
						</Link>
					</ul>
					<Search />
					<div id="hamburger" onClick={show}>
						<svg viewBox="0 0 100 55" width="30" height="20" fill="#fff">
							<rect width="100" height="10"></rect>
							<rect y="25" width="100" height="10"></rect>
							<rect y="50" width="100" height="10"></rect>
						</svg>
					</div>
				</div>
			</nav>
		);
	}
}

export default Nav;
