import React from "react";
import "./Movies.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
const fetch = require("node-fetch");

class Movies extends React.Component {
	constructor() {
		super();
		this.state = { movies: [] };
	}
	componentDidMount() {
		fetch(this.props.url)
			.then((response) => response.json())
			.then((json) => this.setState({ movies: json["results"] }));
	}

	render() {
		const settings = {
			dots: false,
			infinite: false,
			speed: 500,
			slidesToShow: 5,
			slidesToScroll: 3,
			arrows: false,
			responsive: [
				{
					breakpoint: 1000,
					settings: {
						slidesToShow: 3,
						slidesToScroll: 3,
					},
				},
				{
					breakpoint: 600,
					settings: {
						slidesToShow: 2,
						slidesToScroll: 2,
					},
				},
			],
		};
		const mediaForm = this.props.media_form;
		if (this.state.movies.length !== 0) {
			return (
				<div>
					<h2 className="slick_header">{this.props.title}</h2>
					<Slider {...settings}>
						{this.state.movies.map(function (movie) {
							return (
								<div>
									<a href={ mediaForm === 'movie' ? `../screentime/#/movie/${movie.id}`
												: mediaForm === 'tv' ? `../screentime/#/tv/${movie.id}`
												: null} target="_self">
										<div
											className="custom_slick"
											style={{
												backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgb(20, 20, 20, 0.8) 100%), url(https://image.tmdb.org/t/p/w342/${movie.poster_path})`,
											}}
											key={movie.id}
										>
											<p className="custom_slick_title">
												{ mediaForm === 'movie' ? movie.title
												: mediaForm === 'tv' ? movie.name
												: null}
											</p>
										</div>
									</a>
								</div>
							);
						})}
					</Slider>
				</div>
			);
		}
		return <></>;
	}
}

export default Movies;
