import React, { useState, useEffect } from "react";
import "./Search.css";
const fetch = require("node-fetch");

function Search({ match }) {
	const APIKEY = "601afad9072abecf3270711c574913eb";

	useEffect(() => {
		fetchItem();
	}, []);

	const [item, setItem] = useState({});
	const fetchItem = async () => {
		const fetchItem = await fetch(
			`https://api.themoviedb.org/3/search/multi?api_key=${APIKEY}&language=en-US&query=${match.params.query}&page=1&include_adult=false`,
		);
		const item = await fetchItem.json();
		setItem(item);
	};
	if (Object.entries(item).length !== 0 && item["results"].length !== 0) {
		document.getElementById("Search").style.opacity = "1";
	}
	if (Object.entries(item).length !== 0 && item["results"].length !== 0) {
		return (
			<div id="Search" className="load">
				<main className="add_pad">
					<h1>Results for: {match.params.query}</h1>
					{item["results"].map(function (item) {
						if (item.media_type !== "person") {
							return (
								<div className="media_container">
									<a
										href={
											item.media_type === "movie"
												? `../screentime/#/movie/${item.id}`
												: item.media_type === "tv"
												? `../screentime/#/tv/${item.id}`
												: null
										}
									>
										<div
											className="media_wrapper"
											style={{
												backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgb(20, 20, 20, 0.8) 100%), url(https://image.tmdb.org/t/p/w342/${item.poster_path})`,
											}}
											key={item.id}
										>
											<p className="media_title">
												{item.media_type === "movie"
													? item.title
													: item.media_type === "tv"
													? item.name
													: null}
											</p>
										</div>
									</a>
								</div>
							);
						}
					})}
				</main>
			</div>
		);
	}
	return <div id="Search" className="load"></div>;
}

export default Search;
